<template>
    <div class="box">
        <div class="box-container">
            <div class="title">雅韶先锋后台管理</div>
            <div class="inp">
                <!-- <div class="inp-title">账户: </div> -->
                <el-input class="inp-el" prefix-icon="el-icon-user" v-model="form.mobile" placeholder="请输入手机号"></el-input>
            </div>
            <div class="inp">
                <!-- <div class="inp-title">密码: </div> -->
                <el-input class="inp-el" prefix-icon="el-icon-edit" type="password" v-model="form.password" placeholder="请输入密码"></el-input>
            </div>
            <div class="inp">
                <!-- <div class="inp-title">密码: </div> -->
                <el-input class="inp-el" prefix-icon="el-icon-edit" type="password" v-model="twoPassWord" placeholder="请再次输入密码"></el-input>
            </div>
            <div class="inp">
                <el-input class="inp-el" prefix-icon="el-icon-mobile-phone" v-model="form.captcha" placeholder="请输入验证码"></el-input>
                <el-button type="primary" :disabled="disabled" style="margin-right: 20px" @click="getCode">{{disabled ? `${timeStr}秒后重新获取` : '获取验证码'}}</el-button>
            </div>
            <div class="login-inp-box">
                <el-button @click="registerBtn" class="login-inp" type="primary">注册</el-button>
                <div class="register" @click="loginGo">
                    登陆
                </div>
            </div>
        </div>
        <div class="bottom-box">
            Copyright 2022 yashaoxf.com All Rights Reserved. 雅韶先锋 版权所有
            <br>
            <br>
            <div class="copyright-btn" @click="goCopyRight">
                粤ICP备2022139784号-1
            </div>
        </div>
    </div>
</template>

<script>
import { registerUser } from '@/api/login';
export default {
    data () {
        return {
            form: {
                mobile: '',
                password: '',
                captcha: '1388'
            },
            twoPassWord: '',
            disabled: false,
            timeStr: 60
        };
    },
    created () {

    },
    methods: {
        getCode () {
            this.Timer = setInterval(() => {
                if (this.timeStr == 1) {
                    this.timeStr = 60;
                    clearInterval(this.timeStr);
                    this.disabled = false;
                    return;
                }
                this.disabled = true;
                this.timeStr = this.timeStr - 1;
            }, 1000);
        },
        registerBtn () {
            if (!this.form.password || this.form.password !== this.twoPassWord) {
                this.$message.error('请确认再次输入密码和密码一致');
                return;
            }
            const regs = /((?=.*\d)(?=.*\D)|(?=.*[a-zA-Z])(?=.*[^a-zA-Z]))(?!^.*[\u4E00-\u9FA5].*$)/;
            if (!regs.test(this.form.password)) {
                this.$message.error('必须包含数字、字母、符号至少2种');
                return;
            }
            registerUser(this.form).then(res => {
                if (res.data.code == 0) {
                    this.$message.success(res.data.message);
                    setTimeout(() => {
                        this.$router.push({
                            path: '/login'
                        });
                    }, 2000);
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        loginGo () {
            this.$router.push({
                path: '/login'
            });
        },
        goCopyRight () {
            window.open('https://beian.miit.gov.cn');
        }
    }
}
</script>

<style scoped>
.box {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.box-container {
    width: 400px;
    height: 420px;
    box-shadow: 1px 2px 4px #ccc;
    border-radius: 4px;
}

.title {
    font-size: 16px;
    font-weight: bold;
    width: 100%;
    text-align: center;
    padding: 20px 0;
}

.inp {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.inp-title {
    width: 80px;
    text-align: right;
}

.inp-el {
    flex: 1;
    margin-right: 20px;
    margin-left: 20px;
}
.login-inp-box {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
}
.login-inp {
    flex: 1;
    margin-right: 20px;
    margin-left: 20px;
}
.register {
    width: 100%;
    text-align: center;
    padding-top: 30px;
    cursor: pointer;
    font-size: 12px;
}
.bottom-box {
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding-bottom: 20px;
}
.copyright-btn {
    cursor: pointer;
}
</style>